import React from 'react'
import {Helmet} from "react-helmet"

export default props => {

   return (
      <Helmet defaultTitle={props.title || "Real Valladolid - Polska"}>
         <meta charSet="utf-8" />
         <meta name="description" content="Real Valladolid Club de Fútbol – hiszpański klub piłkarski z siedzibą w Valladolid."/>
         <meta name="keywords" content={props.keywords || "Real Valladolid"}/>
         <link rel="icon" href="https://upload.wikimedia.org/wikipedia/en/thumb/6/6e/Real_Valladolid_Logo.svg/220px-Real_Valladolid_Logo.svg.png" />
         <html lang="pl" />
      </Helmet>
   )
}